import {Component} from "react";
import Web3 from 'web3'
import {MetaMaskProvider, useMetaMask} from "metamask-react";

class Form extends Component{


    constructor(props) {
        super(props);
        this.state = {
            matic_address: '',
            data: ''
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleRequestMetamask = this.handleRequestMetamask.bind(this);
    }

    handleChange(event) {
        this.setState({matic_address: event.target.value});
    }

    handleSubmit(event) {
        this.handleRequestApi();
        event.preventDefault();
    }

    addressData(){
        if(this.state.data === '')
            return(<></>);
        return(
        <div className="card bg-gray mt-4">
            {this.state.data.map((key, element) => {
                return( <div className="card-body"> {key.name} : {key.value} </div>)})
            }
        </div>
        );
    };

    isValidMatic(){
        return Web3.utils.isAddress(this.state.matic_address)
    };

    handleRequestApi(){
        this.setState(this.state['data'] = [{name:'depositAddress', value :"Bkce456.."},
                        {name:'balance Confirmed', value:0.01}, {name:'balance Unconfirmed', value:0.16}]);
    }

    async handleRequestMetamask(){
        await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [{
                        chainId: `0x${Number(137).toString(16)}`,
                        chainName: "Polygon Mainnet",
                        nativeCurrency: {
                            name: "MATIC",
                            symbol: "MATIC",
                            decimals: 18
                        },
                        rpcUrls: ["https://polygon-rpc.com/"],
                        blockExplorerUrls: ["https://polygonscan.com/"]
                }
            ]
        });

        await window.ethereum.request({method:'eth_requestAccounts'})
            .then((res)=>{
                // Return the address of the wallet
                this.setState({matic_address: res[0]});
            })
    }

    render(){
        return(
            <>
            <button className="btn btn-sm btn-link w-100 mb-2" onClick={this.handleRequestMetamask}> Connect with Metamask</button>
        <form className="form-floating" onSubmit={this.handleSubmit}>
            <input type="text" className={`form-control bg-dark text-light ${this.isValidMatic()? "is-valid" : "is-invalid"}`} id="bkc_address" placeholder="0x8AeF..."
                   value={this.state.matic_address} onChange={this.handleChange}/>
            <label htmlFor="bkc_address">Address Matic</label>
                <button className={`btn btn-primary btn-lg w-100 mt-2 ${this.isValidMatic() ? "" : "disabled"}`} onClick={this.handleSubmit}>Get Details</button>
            {this.addressData()}
        </form>
            </>
    );
    }
}
export default Form;